<template>
  <div class="tab-control">
    <div
      v-for="(item, index) in titles"
      :key="index"
      class="tab-control-item"
      @click="itemClick(index)"
      :class="{ avtive: Current === index }"
    >
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    titles: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      Current: 0
    }
  },
  computed: {},
  mounted() {},
  methods: {
    itemClick(index) {
      this.Current = index
      this.$emit('tabClick',index)
    }
  },
  components: {}
}
</script>

<style scoped lang="less">
.tab-control {
  display: flex;
}
.tab-control-item {
  flex: 1;
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  span{
      padding: 5px;
  }
}
.avtive span{
  color: #ff8198;
  border-bottom: 2px solid #ff8198;
}
</style>
