<template>
    <div class="goosItem" @click="itemClick">
            <!-- @load 监听图片加载完成后触发函数  -->
            <img :src="goodsItem.show.img" :alt="goodsItem.title" :title="goodsItem.title" @load="imgLoad">     
            <div><p>{{goodsItem.title}}</p>
            <span class="price">￥{{goodsItem.price}}</span>
            <span class="cfav el-icon-star-off">{{goodsItem.cfav}}</span>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        goodsItem:{
            type:Object,
            default(){
                return []
            }
        }
    },
    data() {
        return {}
    },
    computed: {},
    mounted() {},
    methods: {
        imgLoad(){
            // $bus 事件总线
            // $bus 默认没有 需要main 里注册   发出去 事件
            this.$bus.$emit('itemImgLoad')
        },
        itemClick(){
            // 用 push 是因为详情页要返回
            this.$router.push('/detail/'+ this.goodsItem.iid)
        }
    },
    components: {}
    }
</script>

<style scoped lang="less">
.goosItem{
    width: 50%;
    padding: 10px;
    text-align: center;
}
.goosItem img{
    width: 100%;
    border-radius: 3px;
    box-shadow: 0px 1px 4px #979797;
}
.goosItem .price{
    color: red;
}
.goosItem p{
    word-break:break-all;
	display:-webkit-box;
	-webkit-line-clamp:1;
	-webkit-box-orient:vertical;
	overflow:hidden;
    margin:5px 0;
}
.goosItem .price{
margin: 0 5px;
}
</style>
