<template>
  <div>
    <el-carousel height="100%">
      <el-carousel-item
        v-show="banners"
        v-for="(item, index) in banners"
        :key="index"
      >
        <a :href="item.link">
          <div id="bannerImg">
            <img :src="item.image" :alt="item.title" @load="imgLoad" />
          </div>
        </a>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  props: {
    banners: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {}
  },
  components: {},
  computed: {},
  mounted() {},
  updated() {
      const h = document.querySelector('#bannerImg').offsetHeight
      document.querySelector('.el-carousel').style.height = h + 'px'
      window.onresize = function() {
        const h = document.querySelector('#bannerImg').offsetHeight
        document.querySelector('.el-carousel').style.height = h + 'px'
      }
  },
  created() {},
  methods: {
    imgLoad(){
      this.$emit('imgLoad')
    }
  }
}
</script>

<style lang="less" scoped>
.el-carousel__item {
  color: #475669;
  font-size: 14px;
  opacity: 1;
}
.el-carousel__item img {
  width: 100%;
}
</style>
