<template>
  <div id="home">
    <NavBar class="home-nav"><div slot="center">孙壮的购物商场</div></NavBar>
    <!-- tab 切换 用fiexd 和 better-srcoll 有冲突 所以用两个 tab 切换 滚动到特定距离 就显示这个  -->
    <TabControl
      ref="tabcontrol1"
      :titles="['流行', '新款', '精选']"
      @tabClick="tabClick"
      v-show="isTabFixed"
      class="Fixed-TabControl"
    ></TabControl>
    <Scroll
      class="content"
      ref="scroll"
      :probeType="3"
      @scroll="contentScroll"
      :pullupLoad="true"
      @pullingUp="loadMore"
    > 
      <!-- banner图 -->
      <HomeSwiper :banners="banners" @imgLoad.once="swiperImgLoad"></HomeSwiper>
      <RecommendView :recommends="recommends"></RecommendView>
      <FeatureView></FeatureView>
      <!-- 这个 tab 切换 回跟着scroll滚动一直向上 所以上面又复制了一份 -->
      <TabControl
        ref="tabcontrol2"
        :titles="['流行', '新款', '精选']"
        @tabClick="tabClick"
      ></TabControl>
      <GoodsList :goods="goods[currentType].list"></GoodsList>
    </Scroll>
    <BackTop @click.native="backClick" v-show="isShow"></BackTop>
  </div>
</template>

<script>
import NavBar from '../../components/commom/navbar/NavBar'
import HomeSwiper from './childCmps/homeSwiper.vue'
import RecommendView from './childCmps/RecommendView.vue'
import FeatureView from './childCmps/featureViews.vue'
import TabControl from '../../components/content/tabControl/TabControl.vue'
import GoodsList from '../../components/content/goods/GoodsList.vue'
import Scroll from '../../components/commom/scroll/scroll.vue'
import BackTop from '../../components/content/backtop/BackTop.vue'

import { getHomeData, getHomeGoods } from '../../network/home'
export default {
  props: {},
  data() {
    return {
      // 轮播图数据
      banners: [],
      // 轮播图下4个圆图数据
      recommends: [],
      // 商品数据
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] }
      },
      // 当前产品数据类型
      currentType: 'pop',
      // 回到顶部按钮显示隐藏
      isShow:false,
      // 距离顶部的距离
      tabOffsetTop:0,
      // tab切换是否吸顶
      isTabFixed:false,
      // 保存首页滚动的Y值
      saveY:0
    }
  },
  // 进入时触发的函数
  activated(){
    // 先刷新 在滚动到 saveY
    this.$refs.scroll.refresh()
    this.$refs.scroll.scrollTo(0,this.saveY,0)
  },
  // 离开时触发的函数
  deactivated(){
    // 保存Y
    this.saveY = this.$refs.scroll.getScrollY()
  },
  created() {
    // 请求home 数据
    this.GhomeData()
    // 请求商品数据
    this.Ggoods('pop')
    this.Ggoods('new')
    this.Ggoods('sell')
  },
  mounted(){
    // 调用图片加载防抖函数
    const refresh = this.debounce(this.$refs.scroll.refresh,500)
    // 监听图片加载完成
    // on 接受事件
    this.$bus.$on('itemImgLoad',()=>{
    // $bus 事件总线
    // $bus 默认没有 需要main 里注册
    // 图片只要加载完成  就重新计算滚动高度
    // 调用  const refresh
    refresh()
    // this.$refs.scroll.refresh()
    // console.log('重新计算高度');
    })
    // 获取tab切换距离顶部的距离
  },
  methods: {
    // 网络请求的方法
    GhomeData() {
      getHomeData().then(res => {
        this.banners = res.data.banner.list
        this.recommends = res.data.recommend.list
      })
    },
    Ggoods(type) {
      const page = this.goods[type].page + 1
      getHomeGoods(type, page).then(res => {
        this.goods[type].list.push(...res.data.list)
        // console.log(this.goods, 'goods数据')
        this.goods[type].page = 1
        this.$refs.scroll.finishPullUp()
        // refresh：其实就是重新计算一下内容的高度和宽度
        // this.$refs.scroll.scroll.refresh()
      })
    },
    // 事件监听方法
    tabClick(index) {
      this.currentType = index == 0 ? 'pop' : index == 1 ? 'new' : 'sell'
      // switch(index){
      //   case 0:
      //   this.currentType='pop'
      //   break
      //    case 1:
      //   this.currentType='new'
      //   break
      //    case 2:
      //   this.currentType='sell'
      //   break
      // }
      // 让两个 tabcontrol 的类型 保持一致  index 保持一致 就可以判断类型保持一致
      this.$refs.tabcontrol1.Current = index
      this.$refs.tabcontrol2.Current = index
      this.$refs.scroll.scrollTo(0, -this.tabOffsetTop+5, 2000)
    },
    // 点击回到顶部
    backClick() {
      this.$refs.scroll.scrollTo(0, 0, 1000)
    },
    // 监听滚动
    // 因为 y 的值 是负值 所以要先把 y 转化成正值 在去做对比  (-a.y)
    contentScroll(a){
      this.isShow = (-a.y) > 1200
      this.isTabFixed = (-a.y) > this.tabOffsetTop
    },
    // 上拉加载更多
    loadMore(){
      this.Ggoods(this.currentType)
    },
    // 防抖回调函数  闭包
    debounce(func,delay){
      let timer = null
      return function(...args){
        if (timer) clearTimeout(timer)
        timer = setTimeout(()=>{
          func.apply(this,args)
        },delay)
      }
    },
    // 监听轮播图加载完成
    swiperImgLoad(){
    // 这里拿到的是一个组件  组件是没有offsettop属性的  所以要去拿里面具体的元素
    this.tabOffsetTop = this.$refs.tabcontrol2.$el.offsetTop - 40
    // console.log(this.tabOffsetTop);
    }
  },
  computed: {},

  components: {
    NavBar,
    HomeSwiper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
    Scroll,
    BackTop
  }
}
</script>

<style scoped lang="less">
#home {
  // padding-top: 44px;
  height: 100vh;
  position: relative;
}
.home-nav {
  background-color: #ff8198;
  color: #fff;
  // position: fixed;
  // top: 0;
  // left: 0;
  // right: 0;
  // z-index: 20;
}
.tab-control {
  background-color: rgb(247, 247, 247);
}
.content {
  height: calc(100% - 44px);
  overflow: hidden;
} 
.Fixed-TabControl {
  position: absolute;
  width: 100%;
  z-index: 9;
}
</style>